"use client";

import Logo from "./Logo";
import NavItem from "./NavItem";

export default function Navbar() {
  return (
    <nav className="w-full shadow-md py-4 px-8 bg-white flex justify-between items-center">
      
      {/* LEFT: LOGO */}
      <Logo />

      {/* RIGHT: MENU ITEMS */}
      <div className="flex space-x-8">
        <NavItem label="Students" href="/students" />
        <NavItem label="Children" href="/children" />
        <NavItem label="Professionals" href="/professionals" />
        <NavItem label="About" href="/about" />
        <NavItem label="References" href="/references" />
      </div>
    </nav>
  );
}
